ul,li,a,p,h1,h2,h3,h4,h5,h6{
    padding: 0px;
    margin:0px;
    list-style-type: none;
    text-decoration: none;
}
body{
    text-align: center;
}
form{
    padding-top: 20px;
    padding-bottom:20px;
    text-align: center;
    background-color: rgb(50, 60, 146);
    height: 80px;
}
input{
    
    height: 40px;
    border: 1px black;
    border-radius: 4px;

}
.load-more{
    
    border: 1px solid transparent;
    border-radius: 4px;
    padding: 9px 24px ;
    font-family: Roboto, sans-serif;
    color: white;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.92;
    background-color: rgb(24, 24, 129);
    letter-spacing: 0.06em;
    justify-content: center;
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.is-hidden{
   display: none;
}

.gallery{
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px;
    padding-bottom: 20px;
}
